<template>
  <div class="project page-container d-flex">
    <div class="info-wrap pr-8">
      <h3 class="mb-2">Company</h3>
      <p class="mb-8">{{ currentPorject.company }}</p>
      <h3 class="mb-2">Team</h3>
      <p class="mb-8">{{ currentPorject.team }}</p>
      <h3 class="mb-2">Year</h3>
      <p class="mb-8">{{ currentPorject.year }}</p>
      <h3 class="mb-2">My Deliverable</h3>
      <p class="mb-8">{{ currentPorject.deliverable }}</p>
      <h3 class="mb-2">Software</h3>
      <p class="mb-8">{{ currentPorject.software }}</p>
    </div>
    <div class="flex-1">
      <h1>{{ currentPorject.name }}</h1>
      <template
        v-if="
          !!currentPorject.paragraphs && currentPorject.paragraphs.length > 0
        "
      >
        <div v-for="(v, i) in currentPorject.paragraphs" :key="i" class="info-paragraph">
          <h2 class="mb-0 text-primary">{{ v.title }}</h2>
          <template v-for="(item, index) in v.content">
            <div
              :key="index + 'i'"
              v-if="item.type && item.type == 'img'"
              class="info-img"
              :class="item.class"
              :style="{ backgroundImage: `url(${getImg(item.url)})` }"
            >
              <p class="info-desc">{{ item.desc }}</p>
            </div>
            <p class="info-text mt-2" :key="index + 'p'" v-else>{{ item }}</p>
          </template>
        </div>
      </template>
    </div>

    <!--  -->
  </div>
</template>
      
      <script>
import { PROJECT_DETAILS } from "@/constant";
export default {
  name: "ProjectDetail",
  components: {},
  data() {
    return {
      PROJECT_DETAILS,
      projectName: "",
      currentPorject: {},
    };
  },
  computed: {
    getImg() {
      return function (url) {
        return require(`@/assets/${url}`);
      };
    },
  },
  methods: {},
  mounted() {
    const {
      params: { projectName },
    } = this.$route;
    this.projectName = projectName;
    this.currentPorject = { ...this.PROJECT_DETAILS[projectName] };
  },
};
</script>
      
      <style>
@import "../style/project.scss";
</style>
      